import React from 'react';
import {
    Card,
} from 'antd';

/**
 * ViewCard组件
 * @param props
 *  title: 标题
 *  styles: 样式
 *  content: 内容部分
 * @constructor
 */
function Index(props) {
    return (
        <div>
            <Card
                title={props.title}
                style={props.styles}
            >
                {props.content}
            </Card>
        </div>
    );
}

export default Index;



