import React from 'react';
import {
    Modal,
    Button,
} from "antd";
import './index.less';

/**
 * SelectDataModal组件
 * @param props
 *          visible: 控制显示隐藏的，false隐藏，true显示
 *          title: 模态框的标题，默认为modal
 *          content: 主体内容
 *          extraFooter: 拓展在modal脚部的内容
 *          onOk: 点击确定时触发的事件
 *          onCancel: 点击取消和右上角的x时触发的事件
 * @constructor
 */
function Index(props) {

    return (
        <Modal
            visible={props.visible}
            title={props.title || 'modal'}
            className={'modal'}
            width={1130}
            onCancel={() => {
                const { onCancel } = props;
                onCancel && onCancel();
            }}
            footer={
                <div>
                    {props.extraFooter || ''}
                    <Button onClick={() => {
                        const { onCancel } = props;
                        onCancel && onCancel();
                    }}>取消</Button>
                    <Button
                        type="primary"
                        // disabled={selectedRowKeys.length === 0}
                        onClick={() => {
                            const { onOk } = props;
                            onOk && onOk();
                        }}
                    >
                        确定
                    </Button>
                </div>
            }
        >
            {
                props.content
            }
        </Modal>
    );
}

export default Index;



